<template>
  <div class="wl-address">
    <el-select
      @change="provinceChange"
      v-model="province_act"
      placeholder=""
      value-key="code"
      :size="size"
    >
      <el-option
        v-for="item in provinces"
        :key="item.code"
        :label="item.name"
        :value="item"
      >
      </el-option>
    </el-select>
    <el-select
      v-model="city_act"
      :size="size"
      value-key="code"
      placeholder=""
      @change="citysChange"
    >
      <el-option
        v-for="item in citys"
        :key="item.code"
        :label="item.name"
        :value="item"
      >
      </el-option>
    </el-select>
    <el-select
      v-model="county_act"
      :size="size"
      value-key="code"
      placeholder="/"
      @change="countysChange"
    >
      <el-option
        v-for="item in countys"
        :key="item.code"
        :label="item.name"
        :value="item"
      >
      </el-option>
    </el-select>
  </div>
</template>
<script>
import { getProvince, getCity, getCounty } from "./address.js"; // 

export default {
  name: "wlAddress",
  data() {
    return {
      provinces: [], // 
      citys: [], // 
      countys: [], // 
      province_act: {
        code: "",
        name: ""
      }, // 
      city_act: {
        code: "",
        name: ""
      }, // 
      county_act: {
        code: "",
        name: ""
      } // 
    };
  },
  props: {
    // 
    size: {
      type: String,
      default: ""
    },
    // 
    address: {
      type: String,
      default: ""
    }
  },
  created() {
    this.provinces = getProvince();
    this.analysisDefaultAddress();
  },
  methods: {
    // 
    provinceChange(val) {
      this.city_act = {
        code: "",
        name: ""
      };
      this.county_act = {
        code: "",
        name: ""
      };
      this.countys = [];
      this.citys = getCity(val);
    },
    // 
    citysChange(val) {
      this.county_act = {
        code: "",
        name: ""
      };
      this.countys = getCounty(val);
    },
    // 
    countysChange(val) {
      let string_address = JSON.stringify([
        this.province_act,
        this.city_act,
        this.county_act
      ]);
      this.$emit("update:address", string_address);
    },
    // 
    analysisDefaultAddress() {
      if (!this.address) return;
      let address = "";
      try {
        address = JSON.parse(this.address) || [];
      } catch (error) {
        console.log(":" + error);
      }
      if (address instanceof Array) {
        [
          this.province_act = {},
          this.city_act = {},
          this.county_act = {}
        ] = address;

        if (this.province_act.code) {
          this.citys = getCity(this.province_act);
        }

        if (this.city_act.code) {
          this.countys = getCounty(this.city_act);
        }
      }
    }
  },
  watch: {
    address(val) {
      this.analysisDefaultAddress();
    }
  }
};
</script>

<style lang="css">
.wl-address{
  display: flex;
  justify-content: space-between;
}

.wl-address .el-select{
  flex: 0.31;
}
</style>
